import React, { useEffect, useState } from 'react'
import PackageCard from './PackageCard'
import axios from 'axios'

let base_url = "/"

function Packages() {

    let [ packages, setPackages ] = useState([])

    useEffect(()=>{

        axios.get(`${base_url}displayPackages`).then((res)=>{
            setPackages(res.data)

            let tourDestinationsUrls = []

            res.data.forEach((item)=>{
                tourDestinationsUrls.push(`/package/${item.packageGroupId}/${item.name}`)
            })

            let cards = document.querySelectorAll('.card')
            cards.forEach((card,index)=>{
                card.addEventListener('click',()=>{
                    window.location = tourDestinationsUrls[index];
                })
            })

        })


    },[])

    return (
        <div id="pacakges-container">
            <div className="container" id="pacakges">
                <div id="data">
                    <div>
                        <h2>Exciting Hot Deals on Holiday Packages</h2>
                        <p>All inclusive packages with no hidden costs, including transport, meals, hotel accommodation and all major sightseeings.</p>
                    </div>
                    <a href="/packages/all">View all</a>
                </div>        
                <div className="cards">

                    {
                        packages.length > 0 ? (
                            packages.map((item)=>{
                                return <PackageCard days={item.days} nights={item.night} features={item.features} image={`/images/${item.imageUrls[0]}`} title={item.name} price={`â‚¹${item.price}`} />
                            })
                        ) : (
                            <>
                            </>
                        )
                    }

                </div>  
                <a href="/packages/all" >View More</a>  
            </div>
        </div>
    )
}

export default Packages
